import React, { useContext } from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { FormattedMessage, navigate } from 'gatsby-plugin-intl';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Query, AllorsSerialisedItem, AllorsUnifiedGood } from '../../graphql-types';
import { Localised } from '../components/localised';
import { StoreContext } from '../store/Store';
import { Separator } from '../components/separator';
import { useToasts } from 'react-toast-notifications';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ReactMarkdown from 'react-markdown';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';

import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import { BreadCrumbs } from '../components/breadcrumbs';

const ProductPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const { addSerialisedItemId: addSerialisedItemId, addProductId: addProductId } = useContext(StoreContext);

  const { addToast } = useToasts();

  const addProduct = (product: AllorsUnifiedGood) => {
    addProductId(product.allorsId);
    addToast(`Successfully added ${product.name}`, {
      appearance: 'info',
      autoDismiss: true,
    });
    navigate('/request');
  };

  const addSerialisedItem = (serialisedItem: AllorsSerialisedItem) => {
    addSerialisedItemId(serialisedItem.allorsId);
    addToast(`Successfully added ${serialisedItem.name}`, {
      appearance: 'info',
      autoDismiss: true,
    });
    navigate('/request');
  };

  const product = data.allorsUnifiedGood;

  const { brand, model, publicElectronicDocuments, publicLocalisedElectronicDocuments, serialisedItems } = data.allorsUnifiedGood;

  const [serialisedItemAllorsId, setSerialisedItemAllorsId] = React.useState('0');
  const [isOpen, setIsOpen] = React.useState(false);
  const [index, setIndex] = React.useState(0);

  const serialisedItem = serialisedItems?.find((v) => v.allorsId === serialisedItemAllorsId);
  const photos = []
    .concat(serialisedItem?.primaryPhoto)
    .concat(serialisedItem?.secondaryPhotos)
    .concat(serialisedItem?.additionalPhotos)
    .filter((v) => v);
  const urls = photos.filter((photo) => photo.file.childImageSharp).map((photo) => photo.file.childImageSharp.fluid.src);

  const seoImage = product.primaryPhoto?.file?.childImageSharp?.original?.src;
  const productImageFluid = product.primaryPhoto?.file.childImageSharp.fluid;
  const productCharacteristics = data.allorsUnifiedGood.serialisedItemCharacteristics;

  const productCategory = data.allorsUnifiedGood.productCategoriesWhereProduct?.[0];
  const group = productCategory?.primaryParent;
  const family = group?.primaryParent;

  const documents = []
    .concat(publicElectronicDocuments)
    .concat(publicLocalisedElectronicDocuments?.map((v) => v.media))
    .filter((v) => v);

  const noImage = data.allFile.nodes[0].childImageSharp.fluid;

  return (
    <Layout>
      <SEO title={product.name} description={product.seoDescription ?? product.description} image={seoImage} />
      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col xs="12" md="12">
                  <BreadCrumbs section="catalogue" family={family} group={group} product={product} />
                  <h1 className="page-title">{product.name}</h1>
                  <p className="page-description">{product.seoDescription}</p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>

          {isOpen && (
            <Lightbox
              mainSrc={urls[index]}
              nextSrc={urls[(index + 1) % urls.length]}
              prevSrc={urls[(index + urls.length - 1) % urls.length]}
              onCloseRequest={() => setIsOpen(false)}
              onMovePrevRequest={() => setIndex((index + urls.length - 1) % urls.length)}
              onMoveNextRequest={() => setIndex((index + 1) % urls.length)}
            />
          )}

          <Container>
            <Row>
              <Col md="6" xs="12" className="product-shot">
                {productImageFluid ? <Img fluid={productImageFluid} /> : <Img fluid={noImage} />}
              </Col>
              <Col md="6" xs="12">
                <h2 className="product-specs-title">Specifications</h2>
                <Table striped className="product-specs">
                  <tbody>
                    {brand && (
                      <tr>
                        <th scope="row">
                          <FormattedMessage id="brand" />
                        </th>
                        <td>{brand.name}</td>
                      </tr>
                    )}

                    {model && (
                      <tr>
                        <th scope="row">
                          <FormattedMessage id="model" />
                        </th>
                        <td>{model.name}</td>
                      </tr>
                    )}

                    {productCharacteristics &&
                      productCharacteristics.map((characteristic) => {
                        const characteristicType = characteristic?.serialisedItemCharacteristicType;
                        return (
                          <tr key={characteristic.serialisedItemCharacteristicType.name}>
                            <th scope="row">
                              <Localised defaultText={characteristicType?.name} localisedTexts={characteristicType.localisedNames} />
                            </th>
                            <td>
                              <Localised defaultText={characteristic?.value} localisedTexts={characteristic.localisedValues} />
                            </td>
                          </tr>
                        );
                      })}
                  </tbody>
                </Table>
                <div className="product-description-short">
                  <h3>Information</h3>
                  {product?.description && <ReactMarkdown source={product.description} />}
                </div>

                {(!serialisedItems || serialisedItems.length === 0) && (
                  <Form method="post">
                    <Button variant="primary" block value="" onClick={() => addProduct(product)}>
                      Add to request <FontAwesomeIcon icon="angle-right" />
                    </Button>
                  </Form>
                )}
              </Col>
            </Row>
          </Container>

          {documents?.length > 0 && (
            <Container>
              <Row>
                <Col md="12">
                  <Separator />
                </Col>
              </Row>
              <Row>
                <Col className="product-description" id="description">
                  <h2>Documentation</h2>
                  <Table striped hover>
                    <thead>
                      <tr>
                        <th scope="col"></th>
                        <th scope="col" style={{ width: '80%' }}>
                          name
                        </th>
                        <th scope="col">language</th>
                      </tr>
                    </thead>
                    <tbody>
                      {documents.map((document) => {
                        return (
                          <tr key={document.fileName}>
                            <td scope="row">
                              <FontAwesomeIcon icon="file-pdf" style={{ color: '#FF2116' }} />
                            </td>
                            <td>
                              <a href={document.file.publicURL} target="pdf">
                                {document.fileName}
                              </a>
                            </td>
                            <td>{document.Locale?.Name ?? 'EN'}</td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </Container>
          )}

          {serialisedItems?.length > 0 && (
            <Container>
              <Row>
                <Col md="12">
                  <Separator />
                </Col>
              </Row>
              <Row>
                <Col>
                  <h2>Available Units</h2>
                </Col>
              </Row>
              <Row>
                {serialisedItems?.map((serialisedItem) => {
                  const { itemNumber, manufacturingYear } = serialisedItem;
                  const fluid = serialisedItem.primaryPhoto?.file.childImageSharp?.fluid;

                  const itemCharacteristics =
                    serialisedItem.serialisedItemCharacteristics &&
                    serialisedItem.serialisedItemCharacteristics
                      .filter((v) => v?.value || v.localisedValues?.length > 0)
                      .filter((v) => {
                        if (productCharacteristics) {
                          const duplicate = productCharacteristics?.filter(
                            (w) =>
                              w.serialisedItemCharacteristicType.allorsId === v.serialisedItemCharacteristicType.allorsId &&
                              w.value == v.value,
                          ).length;
                          return duplicate == 0;
                        }

                        return true;
                      });

                  return (
                    <Col md="4" key={itemNumber}>
                      <div
                        className="product-shot mb-2 video-wrapper"
                        onClick={() => {
                          setSerialisedItemAllorsId(serialisedItem.allorsId);
                          setIndex(0);
                          setIsOpen(true);
                        }}
                      >
                        {fluid ? (
                          <>
                            <Img fluid={fluid} />
                            <div className="play-btn"></div>
                          </>
                        ) : (
                          <Img fluid={noImage} />
                        )}
                      </div>
                      <div>
                        <Table striped className="product-specs">
                          <tbody>
                            <tr>
                              <th scope="row">ID</th>
                              <td>{itemNumber}</td>
                            </tr>

                            {manufacturingYear && (
                              <tr>
                                <th scope="row">YOM</th>
                                <td>{manufacturingYear}</td>
                              </tr>
                            )}

                            {itemCharacteristics &&
                              itemCharacteristics.map((characteristic) => {
                                const characteristicType = characteristic?.serialisedItemCharacteristicType;
                                return (
                                  <tr key={characteristic.serialisedItemCharacteristicType.name}>
                                    <th scope="row">
                                      <Localised
                                        defaultText={characteristicType?.name}
                                        localisedTexts={characteristicType.localisedNames}
                                      />
                                    </th>
                                    <td>
                                      <Localised defaultText={characteristic?.value} localisedTexts={characteristic.localisedValues} />
                                    </td>
                                  </tr>
                                );
                              })}
                          </tbody>
                        </Table>
                        <div className="product-description-short">
                          <h3>Notes</h3>
                          <p>Completely refurbished and in good shape.</p>
                        </div>
                        <Form method="post">
                          <Button variant="primary" block value="" onClick={() => addSerialisedItem(serialisedItem)}>
                            Add to request <FontAwesomeIcon icon="angle-right" />
                          </Button>
                        </Form>
                      </div>
                    </Col>
                  );
                })}
              </Row>
            </Container>
          )}

          <Container>
            <Row>
              <Col md="12">
                <Separator />
              </Col>
            </Row>
            <Row>
              <Col className="product-manufacturer">
                <h2>About the manufacturer</h2>
                {brand?.description && <ReactMarkdown source={brand.description} />}
              </Col>
            </Row>
          </Container>
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    allorsUnifiedGood(slug: { eq: $slug }) {
      allorsId
      name
      description
      publicElectronicDocuments {
        fileName
        file {
          publicURL
        }
      }
      publicLocalisedElectronicDocuments {
        media {
          fileName
          file {
            publicURL
          }
        }
      }
      productCategoriesWhereProduct {
        slug
        name
        primaryParent {
          slug
          name
          primaryParent {
            slug
            name
            primaryParent {
              slug
              name
            }
          }
        }
      }
      primaryPhoto {
        file {
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
            original {
              src
            }
          }
        }
      }
      photos {
        file {
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
      brand {
        name
        description
      }
      model {
        name
      }
      serialisedItemCharacteristics {
        value
        serialisedItemCharacteristicType {
          name
          localisedNames {
            locale {
              language {
                isoCode
              }
            }
            text
          }
          unitOfMeasure {
            UnitOfMeasure {
              name
              localisedNames {
                locale {
                  language {
                    isoCode
                  }
                }
                text
              }
            }
          }
        }
      }
      serialisedItems {
        allorsId
        name
        itemNumber
        manufacturingYear
        primaryPhoto {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        secondaryPhotos {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        additionalPhotos {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        serialisedItemCharacteristics {
          value
          serialisedItemCharacteristicType {
            name
            localisedNames {
              locale {
                language {
                  isoCode
                }
              }
              text
            }
            unitOfMeasure {
              UnitOfMeasure {
                name
                localisedNames {
                  locale {
                    language {
                      isoCode
                    }
                  }
                  text
                }
              }
            }
          }
        }
      }
    }
    allFile(filter: { name: { in: ["no-image"] } }) {
      nodes {
        name
        childImageSharp {
          id
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  }
`;

export default ProductPage;
