import React from 'react';
import { useIntl } from 'gatsby-plugin-intl';
import { Maybe, AllorsLocalisedText } from '../../graphql-types';

export const Localised: React.FunctionComponent<{ defaultText: string; localisedTexts: Maybe<Array<Maybe<AllorsLocalisedText>>> }> = ({
  defaultText,
  localisedTexts,
}) => {
  const intl = useIntl();

  const localised = localisedTexts?.filter((v) => v.locale?.language?.isoCode === intl.locale);
  const text = localised && localised.length > 0 ? localised[0].text : defaultText;

  return <>{text}</>;
};
